import React, { Fragment } from 'react'
import spinner from '../../img/spinner.gif'

export const Spinner = () => {
    return (
        <Fragment>
             <img
                src={spinner}
                style={{ width: '200px', margin: 'auto', display: 'block', backgroundColor:'inherit'}}
                alt='Loading...'
            /> 
        </Fragment>
    )
}
